*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: helvetica;
  position: relative;
  font-size: 100%;
}
// .gallery_container {
//   padding: 0.9375rem 0;
// }
.gallery_container h1 {
  // margin: 2rem 0;
  padding: 0;
  margin-left: 10px;
  margin-right: 10px;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-size: 3vw;
  font-weight: lighter;
}
.gallery_container .h1container {
  background-color: rgba(34, 34, 34, 0.892);
width: fit-content;
margin: 0 auto;
border-radius: 10px;
}

.gallery_grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 8px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;

  img {
    width: 100%;
    border: 5px solid #fff;
  }
}
.gallery_container {
  padding: 0.9375rem 0;
}
.modal_overlay {
  position: fixed;
  z-index: 1;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
}
