.modal_body .modal_close {
  right: 0;
  top: 0;
  border-radius: 0 0 0 5px;
}
.modal_body .modal_next,
.modal_body .modal_prev {
  right: 0;
  top: calc(50% - 25px);
  border-radius: 5px 0 0 5px;
  height: 50px;
  line-height: 40px;
  font-size: 60px;
}
.modal_body .modal_prev {
  left: 0;
  right: auto;
  border-radius: 0 5px 5px 0;
}
.modal_body {
  display: flex;
  background-color: rgb(196, 196, 196);
  padding: 10px;

}
.gallery_grid .imgBlock {
  position: relative;
  cursor: pointer;
  &:before,
  &:after {
    transition: 0.3s opacity ease;
    opacity: 0;
  }
  &:after {
    // content: "\02194";
    font-size: 80px;
    position: absolute;
    transform: translate3d(-50%, -50%, 0) rotate(-45deg);
    color: #fff;
    left: 50%;
    top: 50%;
    display: block;
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 4px;
    left: 0;
    right: 0;
    background: rgba(#222, 0.5);
    display: block;
  }
  &:hover {
    &:before,
    &:after {
      opacity: 1;
      transition: 0.3s opacity ease;
    }
  }
}





@media only screen and (max-width: 600px) {

  .modal_body .modal_lightdetails  { 
     font-size: .5em; 
  }

}

.modal_body .modal_text_box {
  flex:1
}
.modal_body .body_container {
  margin-left: 20px;
}



.modal_body .modal_header {
  text-align: center;
  font-weight: bold;
  font-size: 26px;
}
.modal_body .modal_lightdetails {
  flex-direction: column;
  text-align: left;
  
  display: flex;
  
}
.modal_body .modal_flex_image {
  max-width: 170px;
  width: 30%;
  height: 30%;
  object-fit: cover;
  border: 5px solid rgb(255, 255, 255);
}

.modal {
  position: fixed;
  z-index: 999;
  width: 50%;
  max-width: 600px;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  // border-radius:10%;
}
